.header {
  border: 3px solid var(--headerOutline);
  height: 10%;
  width: 85%;
  justify-self: flex-start;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
  padding: 0 1rem;
  margin: 2rem 0 5rem 0;
}
@media (min-width: 1366px) {
  .header {
    width: 100%;
    height: auto;
    border-radius: 2rem;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }
}
